"use strict";

import * as types from "../constants/ActionTypes";
import actions from "../actions";
import R from "ramda";

const STATE = { answers: {} };

const parseAnswer = action => {
  let answers = action.payload.answers || [];
  return R.reduce(
    (r, x) => R.merge(r, { [parseInt(x.questionId)]: x.value }),
    {},
    answers
  );
};

export default (state = STATE, action) => {
  switch (action.type) {
    case types.PATIENT_CONSENTED:
      return R.merge(state, R.assoc("consented", true, action.payload));
    case types.SET_ACTIVE_VISIT:
      let answers = parseAnswer(action);
      return R.assoc("answers", answers, state);
    case types.SET_QUESTIONNAIRE:
      return R.merge(state, action.payload);
    case actions.emr.setAnswer.SUCCESS:
      return R.assoc("answers", R.merge(state.answers, action.payload), state);
    case actions.emr.removeAnswer.SUCCESS:
      return R.assoc("answers", R.omit(action.payload, state.answers), state);
    default:
      return state;
  }
};
