"use strict";

import { combineReducers } from "redux";
import * as types from "../constants/ActionTypes";

function byId(state = {}, action) {
  switch (action.type) {
    case types.FETCH_PRODUCTS_RESP:
      return action.payload.byId;
    default:
      return state;
  }
}

function byTag(state = {}, action) {
  switch (action.type) {
    case types.FETCH_PRODUCTS_RESP:
      return action.payload.byTag;
    default:
      return state;
  }
}

function tags(state = {}, action) {
  switch (action.type) {
    case types.FETCH_PRODUCTS_RESP:
      return action.payload.tags;
    default:
      return state;
  }
}

function purchased(state = {}, action) {
  switch (action.type) {
    case types.SET_PURCHASED_PRODUCTS:
      return action.payload;
    default:
      return state;
  }
}

export default combineReducers({
  byId,
  byTag,
  purchased,
  tags
});
