import content from "../../content";
import clientConfig from "../../config";
const butterCms = require("buttercms");
const minimatch = require("minimatch");
const { customizations, pathBlacklist } = require("./sitemap.constants");

const butter = butterCms(clientConfig.butter);

const locationToSitemapTemplate = ({
  path,
  lastMod = 0,
  changeFreq = "weekly",
  priority = 0.5,
  host = clientConfig.siteAddress
}) => `<url>
<loc>${host}${path}</loc>
${lastMod ? `<lastmod>${lastMod}</lastmod>` : ""}
<changefreq>${changeFreq}</changefreq>
<priority>${priority}</priority>
</url>`;

/**
 * Find a changeFreq and priority values for a path (e.g /hair-loss/shampoo) inside of `customizations` object
 */
const matchCustomizations = (customizations, location) => {
  // the fast check for a path
  if (customizations[location.path]) return customizations[location.path];

  // the slow check using globs (includes fallbacks)
  const match = Object.keys(customizations).find(customizationGlob => {
    return minimatch(location.path, customizationGlob);
  });

  return customizations[match];
};

/**
 * merge the customizations with other exsting values
 */
const mergePathsWithMoreInfo = location =>
  R.mergeDeepLeft(location, matchCustomizations(customizations, location));

const transformBlogPostToSitemapLocation = data => ({
  path: "/blog/" + data.url,
  lastMod: data.published
});

const transformBlogCategoryToSitemapLocation = data => ({
  path: "/blog/" + data.slug
});

const transformHardcodedPageToSitemapLocation = path => ({ path });

const removeBlacklistedPaths = R.reject(
  R.propSatisfies(R.contains(R.__, pathBlacklist), "path")
);

const removeDuplicatePaths = R.uniqBy(item => item.path);

module.exports = (request, response) => {
  /**
   * Sitemap spec:
   * https://www.sitemaps.org/protocol.html
   *
   * Date/time format for lastMod:
   * https://www.w3.org/TR/NOTE-datetime
   *
   */

  const contentPageUrls = Object.keys(content.pages).map(
    transformHardcodedPageToSitemapLocation
  );

  const blogPostUrls = butter.post
    .list({ page_size: 9999, exclude_body: true })
    .then(posts => posts.data.data.map(transformBlogPostToSitemapLocation))
    .catch(error => {
      console.error("error getting blog posts for sitemap", error);
      return [];
    });

  const blogCategoryUrls = butter.category
    .list()
    .then(posts => posts.data.data.map(transformBlogCategoryToSitemapLocation))
    .catch(error => {
      console.error("error getting blog categories for sitemap", error);
      return [];
    });

  Promise.all([contentPageUrls, blogPostUrls, blogCategoryUrls])
    .then(
      R.pipe(
        R.flatten,
        removeDuplicatePaths,
        removeBlacklistedPaths,
        R.map(mergePathsWithMoreInfo),
        R.map(locationToSitemapTemplate),
        R.join(""),
        content =>
          `<urlset xmlns="http://www.sitemaps.org/schemas/sitemap/0.9">${content}</urlset>`
      )
    )
    .then(xmlDoc => {
      response.set("Content-Type", "text/xml").send(xmlDoc);
    })
    .catch(error => {
      console.error(error);
      response.status(400).send("error generating sitemap");
    });
};
