"use strict";

import actions from "../actions";
import { combineReducers } from "redux";

function list(
  state = {
    data: [],
    error: null,
    loading: false
  },
  action
) {
  switch (action.type) {
    case actions.coupons.findAllCoupons.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.coupons.findAllCoupons.SUCCESS:
      return {
        ...state,
        data: action.payload
      };
    case actions.coupons.findAllCoupons.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.coupons.findAllCoupons.FULFILL:
      return {
        ...state,
        loading: false
      };
    default:
      return state;
  }
}

export default combineReducers({
  list
});
