import R from "ramda";
import { CONTENTFUL_ROUTINES as ROUTINES } from "./contentful.constants";

const pageResult = (state, action, path = []) => {
  switch (action.type) {
    case ROUTINES.LOAD_ALL_OTHER_PAGES.SUCCESS:
    case ROUTINES.LOAD_PAGE.SUCCESS:
      let { payload } = action;
      let currentState = R.path(path, state);
      return R.assocPath(path, { ...currentState, ...payload }, state);
    default:
      return state;
  }
};

export default pageResult;
