"use strict";

const R = require("ramda");
const REGXPS = require("./regexps");

const normalizeUser = me => {
  return me
    ? R.compose(
        x => R.assoc("prescriptions", R.defaultTo([], x.prescriptions), x),
        x => R.assoc("addresses", R.defaultTo([], x.addresses), x),
        x => R.assoc("cardDetails", R.defaultTo([], x.cardDetails), x)
      )(me)
    : null;
};

const mergeByKey = R.curry((key, xs) => {
  return R.compose(
    R.assoc(key, R.__, {}),
    xs => R.flatten([xs]),
    R.filter(R.identity),
    R.map(R.prop(key))
  )(xs);
});

const urlToHandlerFunction = request => {
  let key;

  if (/\/blog/.test(request.url)) {
    key = "getBlogById";
  } else if (REGXPS.RE_MSG_BY_ID.exec(request.url)) {
    key = "getMessageById";
  } else if (request.url === "/reviews") {
    key = "getReviewsBottomLine";
  } else {
    key = "defaultFn";
  }

  return key;
};

const getTags = R.reduce(
  (r, x) =>
    R.compose(
      R.uniq,
      R.concat(r)
    )(x.tags),
  []
);

const productsByTag = (tags, products) =>
  R.reduce(
    (r, tag) => {
      if (!r[tag]) r[tag] = [];
      r[tag] = R.filter(x => R.contains(tag, x.tags), products);
      return r;
    },
    {},
    tags
  );

const indexById = R.indexBy(R.prop("id"));

const unreadCount = R.reduce((result, message) => {
  return message.read ? result : ++result;
}, 0);

const sortInReverse = R.compose(
  xs => xs.reverse(),
  R.sortBy(R.prop("createdAt"))
);

const mergeProductsWithContent = R.compose(
  R.values,
  indexById
);

const handleError = R.curry((resolve, error) => {
  console.log(error && (error.stack || error.message));
  resolve({ actionsList: [] });
});

const requestToCoupon = R.compose(
  R.toLower,
  R.head,
  R.drop(1),
  R.split("/"),
  R.replace(/\?.+/, ""),
  R.prop("originalUrl")
);

module.exports = {
  getTags,
  handleError,
  indexById,
  mergeByKey,
  mergeProductsWithContent,
  normalizeUser,
  productsByTag,
  requestToCoupon,
  sortInReverse,
  unreadCount,
  urlToHandlerFunction
};
