const defaultState = [];

function routeHistory(state = defaultState, action) {
  switch (action.type) {
    case "ROUTE_CHANGE":
      // if routeHistory is empty add the first item
      if (action.payload === "RESET") {
        return [];
      }
      if (!state[0]) {
        return [
          {
            current: true,
            pathname: action.payload.pathname,
            transition: "SlideIn"
          }
        ];
      }
      // find current route
      const currentRoute = state.findIndex(({ current }) => current);
      // set current route to false
      state[currentRoute].current = false;
      // detect when the next route is coming from going forward
      if (
        state[currentRoute + 1] &&
        state[currentRoute + 1].pathname === action.payload.pathname
      ) {
        state[currentRoute + 1].current = true;
        state[currentRoute + 1].transition = "SlideIn";
        return state;
      }
      // detect when the next route is coming from going backwards
      if (
        state[currentRoute - 1] &&
        state[currentRoute - 1].pathname === action.payload.pathname
      ) {
        state[currentRoute - 1].current = true;
        state[currentRoute - 1].transition = "SlideOut";
        return state;
      }
      // by default push new route to top of array
      state.push({
        current: true,
        pathname: action.payload.pathname,
        transition: "SlideIn"
      });
      return state;
    default:
      return state;
  }
}

export default routeHistory;
