"use strict";

// nnasoody: commented out - https://github.com/Clubroom/store_ui/issues/478

const SCRIPTS = require("./vendor-scripts");

module.exports = ({ content, store, helmet, localhost, styles }) => {
  const state = store.getState();
  const PROJECT_ENV = JSON.stringify(process.env.PROJECT_ENV);
  const PROPS = JSON.stringify(state);
  const gtmEnv = localhost ? "localhost" : state.config.api.stage;
  const gtm = SCRIPTS.GOOGLE_TAG_MANAGER[gtmEnv];
  const BUNDLE_JS = "/bundle.js";
  const fonts = "/bundle.css";

  return `
    <!doctype html>
    <html ${helmet.htmlAttributes.toString()}>
    <head>
      <meta name="viewport" content="width=device-width, height=device-height, initial-scale=1.0, maximum-scale=1.0, user-scalable=0" />
      <meta httpEquiv="content-type" content="text/html;charset=UTF-8" />
      <meta name="p:domain_verify" content="132ba1afe78fdde25f1816cdcfc2c38e"/>
      <link rel="stylesheet" href=${fonts} />
      ${helmet.title.toString()}
      ${helmet.meta.toString()}
      ${helmet.link.toString()}
      ${gtm.script}
      ${styles}
      <script>
        window.PROJECT_ENV=${PROJECT_ENV};
      </script>
      ${SCRIPTS.NEW_RELIC}
      <script>window.PROPS=${PROPS}</script>
      <script>window.dataLayer=window.dataLayer||[]</script>
      ${SCRIPTS.GOOGLE_OPTIMIZE[gtmEnv]}
      ${SCRIPTS.COOKIE_BOT}
      ${SCRIPTS.YOTPO}
      ${SCRIPTS.VWO}
      ${SCRIPTS.GOOGLE_PLACES}
    </head>
    <body className="no-overflow-x" ${helmet.bodyAttributes.toString()}>
      ${gtm.noscript}
      <div id="root">${content}</div>
      <script src=${BUNDLE_JS}></script>
      <script id="stripe-js" src="https://js.stripe.com/v3/" async></script>
    </body>
  </html>`;
};

