"use strict";

import actions from "../actions";
import CONFIG from "../config";

const {
  products: { idByName: productIdByName }
} = CONFIG;

export default (
  state = {
    productId: productIdByName.sildenafil,
    loading: true
  },
  action
) => {
  switch (action.type) {
    case actions.global.setExperiment.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.global.setExperiment.SUCCESS:
      return {
        ...state,
        productId: action.payload,
        loading: false
      };
    case actions.global.setExperiment.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.global.setExperiment.FULFILL:
      return {
        ...state,
        loading: false
      };
    default:
      return state;
  }
};
