"use strict";

const camelize = require("camelize");
const fetch = require("node-fetch");
const R = require("ramda");
const snakeize = require("snakeize");

const getHeaders = headers =>
  R.mergeAll([{ "Content-Type": "application/json" }, headers || {}]);

const isTokenInvalid = response => {
  return R.any(x => new RegExp(x, "ig").test(response.message), [
    "JWT invalid",
    "JWT expired"
  ]);
};

const processServerResponse = R.curry((resolve, reject, response) => {
  if (isTokenInvalid(response)) {
    reject(response);
  } else {
    resolve(camelize(response));
  }
});

function post({ url, payload, headers }) {
  return new Promise((resolve, reject) => {
    // fetch(R.replace("/store", "", url), {
    fetch(url, {
      method: "POST",
      body: JSON.stringify(snakeize(payload)),
      headers: getHeaders(headers)
    })
      .then(response => response.json())
      .then(processServerResponse(resolve, reject))
      .catch(reject);
  });
}

function get({ url, headers }) {
  return new Promise((resolve, reject) => {
    // fetch(R.replace("/store", "", url), {
    fetch(url, {
      method: "GET",
      headers: getHeaders(headers)
    })
      .then(response => response.json())
      .then(processServerResponse(resolve, reject))
      .catch(reject);
  });
}

function put({ url, payload, headers }) {
  return new Promise((resolve, reject) => {
    // fetch(R.replace("/store", "", url), {
    fetch(url, {
      method: "PUT",
      body: JSON.stringify(snakeize(payload)),
      headers: getHeaders(headers)
    })
      .then(response => response.json())
      .then(processServerResponse(resolve, reject))
      .catch(reject);
  });
}

function patch({ url, payload, headers }) {
  return new Promise((resolve, reject) => {
    // fetch(R.replace("/store", "", url), {
    fetch(url, {
      method: "PATCH",
      body: JSON.stringify(snakeize(payload)),
      headers: getHeaders(headers)
    })
      .then(response => response.json())
      .then(processServerResponse(resolve, reject))
      .catch(reject);
  });
}

function del({ url, headers }) {
  return new Promise((resolve, reject) => {
    // fetch(R.replace("/store", "", url), {
    fetch(url, {
      method: "DELETE",
      headers: getHeaders(headers)
    })
      .then(response => response.json())
      .then(processServerResponse(resolve, reject))
      .catch(reject);
  });
}

module.exports = {
  del,
  get,
  patch,
  post,
  put
};
