"use strict";

const FETCH_FUNCTIONS = require("./functions");
const R = require("ramda");
const { handleError, mergeByKey, urlToHandlerFunction } = require("./helpers");

module.exports = request => {
  let handlerKeys = urlToHandlerFunction(request);
  const keys = R.concat(
    [handlerKeys],
    [
      "getBlogCategories",
      "getConfig",
      "getCoupons",
      "getMe",
      "getProducts",
      "getStates"
    ]
  );

  return new Promise(function(resolve) {
    Promise.all(R.map(key => FETCH_FUNCTIONS[key](request), keys))
      .then(mergeByKey("actionsList"))
      .then(resolve)
      .catch(handleError(resolve));
  });
};
