import { combineReducers } from "redux";
import { always, assoc, equals, ifElse, map, mapObjIndexed } from "ramda";

import {
  SHOW_DRAWER,
  HIDE_DRAWER,
  PDP_SCROLL_TO_SAFETY_INFORMATION,
  PDP_SAFETY_INFORMATION_SWITCH_TEXT_CLOSED_STATE,
  DISMISS_COUPON_BANNER
} from "../constants/ActionTypes";
import { DRAWER_POSITIONS, DRAWER_STATES } from "../constants/ui";

const setDrawerStatesByActiveId = (activeId, displayState) => drawers =>
  mapObjIndexed((drawer, drawerId) => {
    const isActiveId = equals(activeId, drawerId);
    return ifElse(
      always(isActiveId),
      assoc("display", displayState),
      assoc("display", DRAWER_STATES.CLOSED)
    )(drawer);
  })(drawers);

const setDrawerStates = displayState => drawers =>
  map(assoc("display", displayState))(drawers);

export const INITIAL_DRAWERS_STATE = {
  [DRAWER_POSITIONS.LEFT]: {
    display: DRAWER_STATES.CLOSED
  },
  [DRAWER_POSITIONS.RIGHT]: {
    display: DRAWER_STATES.CLOSED
  },
  contentId: ""
};

export const drawersReducer = (state = INITIAL_DRAWERS_STATE, action) => {
  switch (action.type) {
    case SHOW_DRAWER: {
      const { side, display, contentId } = action.payload;
      const { left, right } = state;
      let drawers = setDrawerStatesByActiveId(side, display)({ left, right });
      return { ...drawers, contentId };
    }
    case HIDE_DRAWER: {
      const { left, right } = state;
      const drawers = setDrawerStates(DRAWER_STATES.CLOSED)({ left, right });
      return { ...drawers, contentId: "" };
    }
    default:
      return state;
  }
};

export const INITIAL_PDP_SAFETY_INFORMATION_STATE = {
  scrollToSafetyInformationRequestTimestamp: null,
  isClosed: true
};

export const pdpSafetyInformationReducer = (
  state = INITIAL_PDP_SAFETY_INFORMATION_STATE,
  action
) => {
  switch (action.type) {
    case PDP_SCROLL_TO_SAFETY_INFORMATION:
      return {
        scrollToSafetyInformationRequestTimestamp: new Date().getTime(),
        isClosed: false
      };
    case PDP_SAFETY_INFORMATION_SWITCH_TEXT_CLOSED_STATE:
      return {
        ...state,
        isClosed: action.payload.requestedClosedState
      };
    default:
      return state;
  }
};

const INITIAL_COUPON_BANNER_STATE = {
  isDismissed: false
};
export const couponBannerReducer = (
  state = INITIAL_COUPON_BANNER_STATE,
  action
) => {
  switch (action.type) {
    case DISMISS_COUPON_BANNER:
      return {
        isDismissed: true
      };
    default:
      return state;
  }
};

const uiReducer = combineReducers({
  drawers: drawersReducer,
  pdpSafetyInformation: pdpSafetyInformationReducer,
  couponBanner: couponBannerReducer
});

export default uiReducer;
