import { createAction } from "redux-actions";
import * as types from "../constants/ActionTypes";

import { DRAWER_UI } from "../components/drawer/drawer-content.constants";

export const showDrawerWithContentId = createAction(
  types.SHOW_DRAWER,
  contentId => ({
    ...DRAWER_UI[contentId],
    contentId
  })
);

export const showDrawerWithCartAndSwitchToReviewIfNeeded = createAction(
  types.SHOW_DRAWER_CART_OR_FINAL_REVIEW
);
