"use strict";

import * as types from "../constants/ActionTypes";
import actions from "../actions";
import R from "ramda";
import { combineReducers } from "redux";

function removeItemFromOrder(item, order) {
  let items = R.compose(R.filter(x => x.id !== item.id))(order.items);
  return R.merge(order, { items });
}

const hasItem = (item, order) =>
  R.any(x => x.productId === item.productId, order.items);

function updateItemToOrder(item, order) {
  let items;
  if (hasItem(item, order)) {
    items = R.map(
      x => (x.productId !== item.productId ? x : item),
      order.items
    );
  } else {
    items = R.concat(order.items, [item]);
  }
  return R.merge(order, { items });
}

function byId(state = {}, action) {
  switch (action.type) {
    case actions.order.fetchOrders.SUCCESS:
      return action.payload;
    case types.UPDATE_ORDER_RESP:
      return action.payload && action.payload.id
        ? R.merge(state, { [action.payload.id]: action.payload })
        : state;
    case types.CANCEL_ORDER_RESP:
    case types.PATCH_ORDER:
      return action.payload && action.payload.id
        ? R.merge(state, {
            [action.payload.id]: R.merge(
              state[action.payload.id],
              action.payload
            )
          })
        : state;
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return {};
    default:
      return state;
  }
}

function loading(state = true, action) {
  switch (action.type) {
    case actions.order.fetchOrders.REQUEST:
      return true;
    case actions.order.fetchOrders.FULFILL:
      return false;
    default:
      return state;
  }
}

function active(state = null, action) {
  switch (action.type) {
    case actions.order.setCartToOrderById.SUCCESS:
    case types.CREATE_ORDER_RESP:
    case types.UPDATE_ORDER_RESP:
      return action.payload;
    case actions.order.confirmOrder.FAILURE:
      return R.merge(state, action.payload);
    case types.CALCULATE_TOTAL_RESP:
    case types.PATCH_ORDER:
      // NOTE: Occasionally an api call to calculate order totals can be in-flight
      //   after we've cleared the order. Make sure there's still an active order
      return state !== null
        ? R.mergeAll([state, action.payload, { error: null }])
        : state;
    case types.CANCEL_ORDER_RESP:
      return action.payload.id === state.id
        ? R.merge(state, action.payload)
        : state;
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return null;
    case types.UPDATE_ITEM_IN_ORDER_RESP:
      return updateItemToOrder(action.payload, state);
    case types.DELETE_PRODUCT_FROM_CART:
      return removeItemFromOrder(action.payload, state);
    default:
      return state;
  }
}

function cartUpdating(state = false, action) {
  switch (action.type) {
    case actions.order.addProductToCart.REQUEST:
      return true;
    case actions.order.addProductToCart.FULFILL:
      return false;
    default:
      return state;
  }
}

function paymentProcessing(state = false, action) {
  switch (action.type) {
    case actions.order.confirmOrder.REQUEST:
      return true;
    case actions.order.confirmOrder.FULFILL:
    case types.LOGOUT:
      return false;
    default:
      return state;
  }
}

function pendingOrderCheckCompleted(state = false, action) {
  switch (action.type) {
    case types.SET_PENDING_ORDER_CHECK_FLAG:
      return action.payload;
    case types.LOGOUT:
      return false;
    default:
      return state;
  }
}

function coupon(state = null, action) {
  switch (action.type) {
    case types.ADD_COUPON:
      return action.payload;
    case types.LOGOUT:
      return null;
    default:
      return state;
  }
}

export default combineReducers({
  active,
  cartUpdating,
  byId,
  coupon,
  loading,
  paymentProcessing,
  pendingOrderCheckCompleted
});
