"use strict";

import * as types from "../constants/ActionTypes";
import actions from "../actions";
import R from "ramda";
import { combineReducers } from "redux";

function byId(state = {}, action) {
  switch (action.type) {
    case actions.messages.findMessages.SUCCESS:
      return action.payload.byId;
    case types.UPDATE_MESSAGE_RESP:
    case types.MARK_MESSAGE_AS_READ_RESP:
      return R.merge(state, { [action.payload.id]: action.payload });
    case types.LOGOUT:
      return {};
    default:
      return state;
  }
}

function unreadCount(state = 0, action) {
  switch (action.type) {
    case actions.messages.findMessages.SUCCESS:
      return action.payload.unreadCount;
    case types.MARK_MESSAGE_AS_READ_RESP:
      return state - 1;
    case types.LOGOUT:
      return 0;
    default:
      return state;
  }
}

function sorted(state = [], action) {
  switch (action.type) {
    case actions.messages.findMessages.SUCCESS:
      return action.payload.sorted;
    case types.MARK_MESSAGE_AS_READ_RESP:
      return R.map(
        x => (x.id === action.payload.id ? action.payload : x),
        state
      );
    case types.LOGOUT:
      return [];
    default:
      return state;
  }
}

function active(state = null, action) {
  switch (action.type) {
    case types.UPDATE_MESSAGE_RESP:
    case types.SET_ACTIVE_MESSAGE:
      return action.payload;
    default:
      return state;
  }
}

function fetching(state = true, action) {
  switch (action.type) {
    case actions.messages.findMessages.TRIGGER:
      return R.pathOr(true, ["payload"])(action);
    case actions.messages.findMessages.FULFILL:
      return false;
    default:
      return state;
  }
}

export default combineReducers({
  active,
  byId,
  fetching,
  sorted,
  unreadCount
});
