"use strict";
import routes from "../routes";

var actions = require("../actions");
var config = require("../config");
var configureStore = require("./store");
var fetch = require("./fetches");
var HTML = require("./html");
var pathToRegexp = require("path-to-regexp");
var Provider = require("react-redux").Provider;
var R = require("ramda");
var React = require("react");
var ReactDOMServer = require("react-dom/server");
var ReactHelmet = require("react-helmet");
var ReactResponsiveRedux = require("react-responsive-redux");
var ReactRouter = require("react-router");
var ReactRouterRedux = require("react-router-redux");
var router = require("express").Router();
var StyledComponents = require("styled-components");
const path = require("path");
const siteMapController = require("./sitemap/sitemap.controller");

// Constants
// ---

const { createMemoryHistory, match, RouterContext } = ReactRouter;
const { setMobileDetect, mobileParser } = ReactResponsiveRedux;
const { syncHistoryWithStore } = ReactRouterRedux;
const { ServerStyleSheet, StyleSheetManager } = StyledComponents;

const REGXPS = {
  RE_FEED: pathToRegexp("/feed")
};

// Functions
// ===

function isLocalhost(request) {
  return (
    request.get("host").indexOf("localhost") > -1 ||
    request.get("host").indexOf("ngrok") > -1
  );
}

function downloadProductFeed(request) {
  return REGXPS.RE_FEED.exec(request.url);
}

// Main Handler for all requests
// ===

const handler = R.curry((request, response, params) => {
  const memoryHistory = createMemoryHistory(request.url);
  const store = configureStore(memoryHistory);
  const history = syncHistoryWithStore(memoryHistory, store);
  // Add token to createStore
  if (request.cookies.token) {
    store.dispatch(actions.auth.setToken(request.cookies.token));
  }
  // React-responsive component setup
  const mobileDetect = mobileParser(request); // do our mobile detection
  store.dispatch(setMobileDetect(mobileDetect)); // set mobile detection for our responsive store

  // Dispatch actions after fetch to update store
  R.forEach(
    action => store.dispatch(action),
    R.concat([actions.global.updateConfig(config)], params.actionsList || [])
  );
  // Process request!
  match(
    {
      routes: routes(store),
      location: request.url,
      history
    },
    function(error, redirectLocation, renderProps) {
      if (error) {
        // If error exists, handle it like an Internal Server Error:
        response.status(500).send(error.message);
      } else if (redirectLocation) {
        // If redirectLocation exists, then have Express perform a server redirect:
        response.redirect(
          301,
          redirectLocation.pathname + redirectLocation.search
        );
      } else if (renderProps) {
        const sheet = new ServerStyleSheet();
        const content = ReactDOMServer.renderToString(
          <Provider store={store}>
            <StyleSheetManager sheet={sheet.instance}>
              <RouterContext {...renderProps} />
            </StyleSheetManager>
          </Provider>
        );
        const styleTags = sheet.getStyleTags();
        const helmet = ReactHelmet.Helmet.renderStatic();
        const html = HTML({
          content,
          store,
          helmet,
          styles: styleTags,
          localhost: isLocalhost(request)
        });
        response.send(html);
      } else {
        response.status(404).send("not found!");
      }
    }
  );
});

router.get("/sitemap.xml", siteMapController);

router.get("*", (request, response) => {
  if (downloadProductFeed(request)) {
    const productFeedPath = path.join(
      __dirname,
      "public",
      "downloads",
      "product_feed.csv"
    );
    response.download(productFeedPath);
  } else {
    fetch(request)
      .then(handler(request, response))
      .catch(error => {
        console.log(error);
        handler(request, response, error);
      });
  }
});

module.exports = router;
