"use strict";

import * as types from "../constants/ActionTypes";
import STEPS from "../constants/CheckoutSteps";
import { combineReducers } from "redux";

function step(state = STEPS.CART, action) {
  switch (action.type) {
    case types.UPDATE_CHECKOUT_STEP:
      return action.payload;
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return STEPS.CART;
    default:
      return state;
  }
}

export default combineReducers({
  step
});
