/**
 * Sitemap configuration.
 * Different URLs/groups of URLs have different settings
 */
const homePageProperties = {
  changeFreq: "daily",
  priority: 1
};

const pdpProperties = {
  changeFreq: "daily",
  priority: 0.9
};

const blogPostProperties = {
  changeFreq: "monthly",
  priority: 0.8
};

const blogIndexProperties = {
  changeFreq: "daily",
  priority: 0.8
};

const treatmentsProperties = {
  changeFreq: "weekly",
  priority: 0.7
};

const tertiaryProperties = {
  changeFreq: "weekly",
  priority: 0.2
};

/**
 * Sitemap configs applied to different URLs
 * test possible URLs against patterns with http://www.globtester.com
 */
export const customizations = {
  "/": homePageProperties,
  "/blog": blogIndexProperties,
  "/blog/hair": blogIndexProperties,
  "/blog/sex'": blogIndexProperties,
  "/blog/*": blogPostProperties,
  "/hair-loss": pdpProperties,
  "/ed": pdpProperties,
  "/treatment": treatmentsProperties,
  "/treatment/*": treatmentsProperties,
  // fallback for all other paths
  "/**": tertiaryProperties
};

/**
 * paths that should be excluded from the site map
 */
export const pathBlacklist = ["/orders/confirm"];
