"use strict";

import * as types from "../constants/ActionTypes";
import actions from "../actions";
import R from "ramda";
import { combineReducers } from "redux";

const TREATMENTS_INITIAL_STATE = {
  data: null,
  error: null,
  loading: false
};

function questionsInView(state = {}, action) {
  switch (action.type) {
    case types.ADD_TO_QUESTIONS_IN_VIEW:
      let newState = R.merge(state, { [action.payload]: true });
      return newState;
    case types.REMOVE_FROM_QUESTIONS_IN_VIEW:
      return R.omit([`${action.payload}`], state);
    case types.RESET_EMR_FORMS:
    case types.CLEAR_QUESTIONS_IN_VIEW:
    case types.LOGOUT:
      return {};
    default:
      return state;
  }
}

function timestamp(state = null, action) {
  switch (action.type) {
    case types.UPDATE_TIME_STAME_ON_FORM_VALIDATION:
      return action.payload;
    case types.RESET_EMR_FORMS:
    case types.LOGOUT:
      return null;
    default:
      return state;
  }
}

function questionnaires(state = {}, action) {
  switch (action.type) {
    case types.SET_QUESTIONNAIRE:
      return R.merge(state, {
        [`${action.payload.version}_${action.payload.id}`]: action.payload
      });
    case types.RESET_EMR_FORMS:
    case types.LOGOUT:
      return {};
    default:
      return state;
  }
}

function consentGiven(state = false, action) {
  switch (action.type) {
    case types.PATIENT_CONSENTED:
      return true;
    case types.RESET_EMR_FORMS:
    case types.LOGOUT:
      return false;
    default:
      return state;
  }
}

function treatments(state = TREATMENTS_INITIAL_STATE, action) {
  switch (action.type) {
    case actions.treatment.findTreatmentByCode.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.treatment.findTreatmentByCode.SUCCESS:
      return {
        ...state,
        data: R.merge(state.data, action.payload)
      };
    case actions.treatment.findTreatmentByCode.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.treatment.findTreatmentByCode.FULFILL:
      return {
        ...state,
        loading: false
      };
    default:
      return state;
  }
}

export default combineReducers({
  consentGiven,
  questionnaires,
  questionsInView,
  timestamp,
  treatments
});
