"use strict";

import R from "ramda";
import actions from "../actions";
import { combineReducers } from "redux";
import * as types from "../constants/ActionTypes";

function byId(state = {}, action) {
  switch (action.type) {
    case actions.emr.findVisitById.SUCCESS:
      return R.merge(state, { [action.payload.id]: action.payload });
    case types.FETCH_VISITS_RESP:
      return action.payload.byId;
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return {};
    default:
      return state;
  }
}

function active(state = null, action) {
  switch (action.type) {
    case types.PATIENT_CONSENTED:
      return R.merge(state, R.assoc("consented", true, action.payload));
    case actions.emr.findVisitById.SUCCESS:
      return action.payload;
    case types.SET_ACTIVE_VISIT:
      return action.payload;
    case types.RESET_EMR_FORMS:
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return null;
    default:
      return state;
  }
}

function visible(state = false, action) {
  switch (action.type) {
    case types.SHOW_EMR_FORM:
      return true;
    case types.HIDE_EMR_FORM:
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return false;
    default:
      return state;
  }
}

function activeOrderHasPendingVisit(state = false, action) {
  switch (action.type) {
    case actions.checkout.activeOrderHasPendingVisit.TRIGGER:
      return true;
    case actions.checkout.activeOrderHasPendingVisit.FULFILL:
      return false;
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return false;
    default:
      return state;
  }
}

function activeOrderNeedsFinalReviewConfirmation(state = false, action) {
  switch (action.type) {
    case actions.checkout.activeOrderFinalReviewConfirmationStep.TRIGGER:
      return true;
    case actions.checkout.activeOrderFinalReviewConfirmationStep.FULFILL:
      return false;
    case types.LOGOUT:
    case types.CLEAR_ACTIVE_ORDER:
      return false;
    default:
      return state;
  }
}

export default combineReducers({
  active,
  byId,
  visible,
  activeOrderHasPendingVisit,
  activeOrderNeedsFinalReviewConfirmation
});
