"use strict";

import * as types from "../constants/ActionTypes";
import actions from "../actions";
import CONFIG from "../config";
import CONTACT from "../constants/Contact";
import R from "ramda";

const {
  products: { idByName: productIdByName }
} = CONFIG;

// State keys

function config(state = null, action) {
  switch (action.type) {
    case types.SET_CONFIG_DATA:
      return action.payload;
    case types.UPDATE_CONFIG:
      return R.merge(state || {}, action.payload);
    default:
      return state;
  }
}

function token(state = null, action) {
  switch (action.type) {
    case types.SIGN_IN_RESP:
      return action.payload.token;
    case types.SET_TOKEN:
      return action.payload;
    case types.LOGOUT:
      return null;
    default:
      return state;
  }
}

function me(state = null, action) {
  switch (action.type) {
    case types.SIGN_IN_RESP:
      return action.payload.me;
    case types.UPDATE_USER_RESP:
    case types.FETCH_ADDRESSES_RESP:
    case actions.auth.fetchMe.SUCCESS:
      return action.payload;
    case types.LOGOUT:
      return null;
    default:
      return state;
  }
}

function modal(state = {}, action) {
  switch (action.type) {
    case types.OPEN_MODAL:
      return Object.assign(action.payload, { isOpen: true });
    case types.CLOSE_MODAL:
      return R.merge(state || {}, { isOpen: false });
    case types.LOGOUT:
      return {};
    default:
      return state;
  }
}

function toast(state = {}, action) {
  switch (action.type) {
    case types.SHOW_TOAST:
      return Object.assign(action.payload, { isOpen: true });
    case types.HIDE_TOAST:
      return R.merge(state || {}, { isOpen: false });
    case types.LOGOUT:
      return {};
    default:
      return state;
  }
}

function globalCoupon(state = null, action) {
  switch (action.type) {
    case types.SET_GLOBAL_COUPON:
      return action.payload;
    case types.LOGOUT:
      return null;
    default:
      return state;
  }
}

const DEFAULT_IS_COMPUTING_ACTION_ITEMS = {
  messages: false,
  visits: false,
  orders: false
};

function isComputingActionItems(
  state = DEFAULT_IS_COMPUTING_ACTION_ITEMS,
  action
) {
  switch (action.type) {
    case types.FETCH_VISITS_RESP:
      return R.merge(state, { visits: true });
    case actions.order.fetchOrders.FULFILL:
      return R.merge(state, { orders: true });
    case actions.messages.findMessages.FULFILL:
      return R.merge(state, { messages: true });
    case types.LOGOUT:
      return DEFAULT_IS_COMPUTING_ACTION_ITEMS;
    default:
      return state;
  }
}

function treatmentPlanByProductId(
  state = {
    [productIdByName.sildenafil]: "/treatment/ed",
    [productIdByName.finasteride]: "/treatment/hair-loss"
  }
) {
  return state;
}

function licenses(state = {}, action) {
  switch (action.type) {
    case actions.global.findLicenses.SUCCESS:
      return action.payload;
    default:
      return state;
  }
}

function states(
  state = {
    data: null,
    error: null,
    loading: false
  },
  action
) {
  switch (action.type) {
    case actions.global.findStates.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.global.findStates.SUCCESS:
      return {
        ...state,
        data: action.payload
      };
    case actions.global.findStates.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.global.findStates.FULFILL:
      return {
        ...state,
        loading: false
      };
    default:
      return state;
  }
}

function reactivate(
  state = {
    error: null,
    loading: false,
    success: false
  },
  action
) {
  switch (action.type) {
    case actions.subscriptions.reactivateSubscription.TRIGGER:
      return { ...state, loading: true };
    case actions.subscriptions.reactivateSubscription.SUCCESS:
      return { ...state, success: true };
    case actions.subscriptions.reactivateSubscription.FAILURE:
      return { ...state, error: action.payload };
    case actions.subscriptions.reactivateSubscription.FULFILL:
      return { ...state, loading: false };
    default:
      return state;
  }
}

function contact(state = CONTACT) {
  return state;
}

module.exports = {
  config,
  contact,
  globalCoupon,
  isComputingActionItems,
  licenses,
  me,
  modal,
  reactivate,
  states,
  token,
  toast,
  treatmentPlanByProductId
};
