"use strict";
import * as types from "../constants/ActionTypes";
import actions from "../actions";
import R from "ramda";

const DEFAULT_STATE = {
  data: [],
  error: null,
  loading: true
};

const replaceItemInListByKey = (item, xs, key) =>
  R.map(x => (item[key] === x[key] ? item : x), xs);

const updateItemInListByOrderId = (update, previous) =>
  previous.map(
    previousItem =>
      previousItem.orderId === update.orderId
        ? { ...previousItem, ...update }
        : previousItem
  );

export default (state = DEFAULT_STATE, action) => {
  switch (action.type) {
    case actions.subscriptions.fetchSubscriptions.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.subscriptions.fetchSubscriptions.SUCCESS:
      return {
        ...state,
        data: action.payload
      };
    case actions.subscriptions.findSubscriptionById.SUCCESS:
      return {
        ...state,
        data: replaceItemInListByKey(action.payload, state.data, "orderId")
      };
    case actions.subscriptions.fetchSubscriptions.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.subscriptions.fetchSubscriptions.FULFILL:
      return {
        ...state,
        loading: false
      };
    case types.DISMISS_REACTIVATE_SUBSCRIPTION_PROMPT:
      const stateClone = R.clone(state);
      const dismissedSubscriptionIndex = stateClone.data.findIndex(
        sub => sub.orderId === action.payload
      );
      stateClone.data[dismissedSubscriptionIndex].dismissed = true;
      return stateClone;
    case actions.subscriptions.reactivateSubscription.SUCCESS:
      return {
        ...state,
        data: updateItemInListByOrderId(action.payload, state.data)
      };
    case types.LOGOUT:
      return DEFAULT_STATE;
    default:
      return state;
  }
};
