"use strict";

const actions = require("../../actions");
const buttercms = require("buttercms");
const config = require("../../config");
const http = require("../http");
const R = require("ramda");
const utils = require("../../utils");
const camelize = require("camelize");
const REGXPS = require("./regexps");
const {
  getTags,
  handleError,
  indexById,
  normalizeUser,
  productsByTag,
  requestToCoupon
} = require("./helpers");

const { paths } = require("../../routes");
// Constants
// ---

const api = config.api;
const yotpo = config.yotpo;

const butter = buttercms(config.butter);

const defaultFn = () => {
  return new Promise(function(resolve) {
    resolve({
      actionsList: [actions.global.updateConfig(config)]
    });
  });
};

const getConfig = () => {
  return new Promise(function(resolve) {
    http
      .get({ url: `${api.url}/${api.env}/${api.paths.config}` })
      .then(
        R.compose(
          R.prop("config"),
          R.head
        )
      )
      .then(config => {
        resolve({
          actionsList: [actions.global.updateConfig(config)]
        });
      })
      .catch(handleError(resolve));
  });
};

const getProducts = request => {
  return new Promise(function(resolve) {
    let url = `${api.url}/${api.env}/${api.paths.products}`;
    http
      .get({ url })
      .then(products => {
        let tags = getTags(products);
        // create the action
        resolve({
          actionsList: [
            actions.fetch.fetchProductsResp({
              byId: indexById(products),
              byTag: productsByTag(tags, products),
              tags
            })
          ]
        });
      })
      .catch(handleError(resolve));
  });
};

const getCoupons = request => {
  let extractedPath = requestToCoupon(request);
  return new Promise(resolve => {
    // No coupon in the URL, no need to make the call
    if (!extractedPath || paths[extractedPath]) {
      resolve({ actionsList: [] });
      return;
    }
    http
      .get({
        headers: {
          Accept: "application/json",
          Prefer: "return=representation"
        },
        url: `${api.url}/${api.env}/${
          api.paths.couponPages
        }?page=eq.${extractedPath}`
      })
      .then(R.filter(x => x.page === extractedPath))
      .then(R.map(x => R.assoc("productId", R.trim(x.productId), x)))
      .then(coupons =>
        resolve({
          actionsList: [actions.coupons.findAllCoupons.success(coupons)]
        })
      )
      .catch(error => {
        handleError(resolve, error);
      });
  });
};

const getBlogCategories = request => {
  let payload = {};
  const defaultCategories = [{ name: "All", slug: "" }];

  return new Promise(function(resolve) {
    butter.category
      .list()
      .then(camelize)
      .then(response => {
        let categories = response.data;

        categories = R.assoc(
          "data",
          R.concat(defaultCategories, categories.data),
          categories
        );

        payload = {
          actionsList: [actions.blog.findBlogCategories.success(categories)]
        };
        if (!request) {
          payload = categories.data;
        }

        resolve(payload);
      })
      .catch(error => {
        handleError(resolve, error);
      });
  });
};

const getBlogById = request => {
  const blogId = REGXPS.RE_BLOG_BY_ID.exec(request.url);
  const slug = blogId ? blogId[1] : "";
  const page = R.pathOr(1, ["query", "page"], request);

  let butterKey = "retrieve";
  let payload = slug;
  let blogAction = "findBlogBySlug";

  return new Promise(function(resolve) {
    getBlogCategories()
      .then(categories => {
        const isCategory = R.any(R.propEq("slug", slug), categories);

        if (isCategory) {
          blogAction = "findBlogsByCategory";
          butterKey = "list";
          payload = { page, page_size: 10, category_slug: slug };
        }

        butter.post[butterKey](payload)
          .then(camelize)
          .then(response => {
            let data = R.path(["data", "data"], response);

            // Update state;
            if (data) {
              if (R.is(Array, data) && isCategory) {
                const blogsWithPage = R.map(R.assoc("page", page), data);

                data = R.merge(response.data, {
                  data: blogsWithPage,
                  categorySlug: slug
                });
              }

              resolve({
                actionsList: [actions.blog[blogAction].success(data)]
              });
            }
          })
          .catch(error => {
            handleError(resolve, error);
          });
      })
      .catch(error => {
        handleError(resolve, error);
      });
  });
};

const getMe = request => {
  return new Promise(function(resolve) {
    if (request.cookies.token) {
      let token = request.cookies.token;
      http
        .get({
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${token}`,
            Prefer: "return=representation"
          },
          url: `${api.url}/${api.env}/${api.paths.me}`
        })
        .then(R.head)
        .then(user => {
          resolve({
            actionsList: [
              actions.auth.signInResp({
                token: token,
                me: normalizeUser(user)
              })
            ]
          });
        })
        .catch(handleError(resolve));
    } else {
      resolve({ actionsList: [] });
    }
  });
};

const getStates = request => {
  return new Promise(function(resolve) {
    http
      .get({
        url: `${api.url}/${api.env}/${api.paths.states}?order=name.asc`
      })
      .then(states => {
        resolve({
          actionsList: [
            actions.global.findStates.success(utils.sortStates(states)),
            actions.global.findLicenses.success({
              hair_loss: utils.getStatesWithHairLicense(states),
              ed: utils.getStatesWithEdLicense(states),
              cold_sore: utils.getStatesWithColdSoreLicense(states),
              skincare_acne: utils.getStatesWithSkinCareAcneLicense(states),
              skincare_aging: utils.getStatesWithSkinCareAgingLicense(states)
            })
          ]
        });
      })
      .catch(handleError(resolve));
  });
};

const getReviewsBottomLine = request => {
  return new Promise(function(resolve) {
    http
      .get({
        authorize: false,
        url: `${yotpo.url}/${yotpo.paths.bottomline}`
      })
      .then(({ response }) => {
        resolve({
          actionsList: [actions.reviews.fetchBottomLine(response)]
        });
      });
  });
};

module.exports = {
  defaultFn,
  getConfig,
  getBlogCategories,
  getBlogById,
  getCoupons,
  getMe,
  getProducts,
  getReviewsBottomLine,
  getStates
};
