"use strict";

import * as types from "../constants/ActionTypes";
import actions from "../actions";
import { combineReducers } from "redux";

const ALL_CATEGORY = "";

const ACTIVE_DEFAULT_STATE = {
  data: null,
  error: null,
  loading: false
};

function active(state = ACTIVE_DEFAULT_STATE, action) {
  switch (action.type) {
    case actions.blog.findBlogBySlug.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.blog.findBlogBySlug.SUCCESS:
      return {
        ...state,
        data: action.payload
      };
    case actions.blog.findBlogBySlug.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.blog.findBlogBySlug.FULFILL:
      return {
        ...state,
        loading: false
      };
    case types.RESET_ACTIVE_BLOG:
      return ACTIVE_DEFAULT_STATE;
    default:
      return state;
  }
}

const DEFUALT_SCOPE_STATE = {
  categorySlug: ALL_CATEGORY,
  meta: {
    nextPage: 2,
    previousPage: null
  },
  data: [],
  error: null,
  loading: false
};

function scope(state = DEFUALT_SCOPE_STATE, action) {
  switch (action.type) {
    case actions.blog.findBlogsByCategory.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.blog.findBlogsByCategory.SUCCESS:
      return {
        ...state,
        ...action.payload
      };
    case actions.blog.findBlogsByCategory.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.blog.findBlogsByCategory.FULFILL:
      return {
        ...state,
        loading: false
      };
    case types.RESET_ACTIVE_BLOGS:
      return DEFUALT_SCOPE_STATE;
    default:
      return state;
  }
}

const CATEGORIES_DEFAULT_STATE = {
  data: [],
  error: null,
  loading: false
};

function categories(state = CATEGORIES_DEFAULT_STATE, action) {
  switch (action.type) {
    case actions.blog.findBlogCategories.TRIGGER:
      return {
        ...state,
        loading: true
      };
    case actions.blog.findBlogCategories.SUCCESS:
      return {
        ...state,
        data: action.payload.data
      };
    case actions.blog.findBlogCategories.FAILURE:
      return {
        ...state,
        error: action.payload
      };
    case actions.blog.findBlogCategories.FULFILL:
      return {
        ...state,
        loading: false
      };
    default:
      return state;
  }
}

export default combineReducers({
  active,
  categories,
  scope
});
